<style lang="less">
  @import "./onlineUser.less";
</style>
<template>
<div class="search">
  <Row>
    <Col>
      <Card>
        <Form inline :label-width="70" class="search-form">
          <Form-item label="搜索用户">
            <Input type="text" v-model="searchKey" clearable placeholder="请输入搜索关键词" style="width: 300px"/>
          </Form-item>
          <Form-item style="margin-left:-35px;">
            <Button @click="getOnlineUserList"  type="primary" icon="search">搜索</Button>
            <Button @click="handleReset" type="ghost" >重置</Button>
          </Form-item>
        </Form>

        <Row>
          <Alert show-icon>
            已选择 <span class="select-count">{{selectCount}}</span> 项
            <a class="select-clear" @click="clearSelectAll">清空</a>
          </Alert>
        </Row>

        <Row class="margin-top-10 searchable-table-con1">
          <Table :loading="loading" border :columns="columns" :data="data" ref="table" sortable="custom"
                 @on-sort-change="changeSort" @on-selection-change="changeSelect"></Table>
        </Row>
        <Row type="flex" justify="end" class="code-row-bg page">
          <Page :current="this.pageNumber" :total="total" :page-size="this.pageSize" @on-change="changePage"
                @on-page-size-change="changePageSize" :page-size-opts="[10,20,50,100]" size="small" show-total show-elevator show-sizer></Page>
        </Row>
      </Card>

    </Col>

  </Row>

</div>
</template>

<script>
    export default {
      name: "onlineUser",
      data(){
        return{
          loading: true,
          searchKey:"",
          selectList: [],//选中行
          selectCount: 0,//选中的数目
          sortColumn: "userIp", //排序字段
          sortType: "desc",//排序方式
          //表格字段列表
          columns:[
            //第一列 有选择框
            {
              type: "selection",
              width: 60,
              align: "center"
            },
            {
              title: "用户ID",
              key: "userId",
              sortable: true
            },
            {
              title: "连接IP",
              key: "userIp",
              sortable: true
            },
            {
              title: "连接客户端",
              key: "osName",
              sortable: true
            },
            {
              title: "用户Tag",
              key: "userTag",
              sortable: true
            }, {
              title: "操作",
              key: "action",
              align: "center",
              render:(h,params)=>{
                return h("div",[
                  h("Button",{
                    props:{
                      type: "error",
                      size: "small"
                    },
                    on:{
                      //按钮点击事件
                      click:()=>{
                        this.killUser(params.row);
                      }
                    }
                  },
                  "下线")
                ])
              }
            }
          ],
          //表格绑定的数据
          data: [],
          pageNumber: 1,//页号
          pageSize: 10,//页大小
          total: 0//总的数据量大小，请求完数据需要重新设置
        }
      },
      //自定义事件
      methods:{
        init() {
          this.getOnlineUserList();
        },
        //点击分页插件，某页时，触发
        changePage(v) {
          this.pageNumber = v;
          this.getOnlineUserList();
        },
        //页面大小改变时触发
        changePageSize(v) {
          this.pageSize = v;
          this.getOnlineUserList();
        },
        getOnlineUserList() {
          this.loading = true;
          let params = "";
          let url = "push/getOnlineUser";

          params = {
            key: this.searchKey,
            pageNumber: this.pageNumber,
            pageSize: this.pageSize,
            sort: this.sortColumn,
            order: this.sortType
          };
          //模拟数据
          this.data = [
            {"userId": "00001", "userIp": "127.0.0.1", "osName": "IOS", "userTag": "jojo"},
            {"userId": "00002", "userIp": "127.0.0.1", "osName": "Android", "userTag": "jojo"},
            {"userId": "00003", "userIp": "127.0.0.1", "osName": "IOS", "userTag": "jojo"}
          ];
          this.loading=false;
          this.total="3";

        },
        handleReset(){
          this.searchKey="";
          this.getOnlineUserList()
        },
        //踢除用户
        killUser(row){
          //弹出框
          this.$Modal.confirm({
            title: "确认踢除",
            content: "您确认要踢除"+row.userId+"吗？",
            onOk: () => {
              // this.deleteRequest("/log/delByIds", { ids: v.id }).then(res => {
              //   if (res.success === true) {
                  this.$Message.success("踢除成功");
                  this.init();
              //   }
              // });
            }
          });
        },
        clearSelectAll() {
          this.$refs.table.selectAll(false);
        },
        //选中某项时触发
        changeSelect(e) {
          this.selectList = e;
          this.selectCount = e.length;
        },
        //排序改变时触发
        changeSort(e) {
          this.sortColumn = e.key;
          this.sortType = e.order;
          if (e.order === "normal") {
            this.sortType = "";
          }
          this.getOnlineUserList();
        }
      },
      //生命周期方法，el 被新创建的 vm.$el 替换，并挂载到实例上去之后调用该钩子。
      //编译好的html 挂载到页面完成后执行该方法，就是页面加载完后自动执行这个方法，
      // 一般做ajax 网络请求加载数据，
      //这个方法在整个实例中只执行一次
      mounted() {
        this.init();
      }

    }
</script>




